* {
  box-sizing: border-box;
}

@font-face {
  font-family: 'helvetica';
  src: url('/assets/fonts/Helvetica.ttf');
  font-weight: normal;
  font-style: normal;
}

body,
html,
#root {  
  margin: 0;
  width: 100%;
  height: 100%;
  font-family: helvetica, sans-serif;  
  -webkit-font-smoothing: antialiased;  
  font-weight: 600;
  scroll-behavior: smooth;
}